html,
body {
  height: 100%;
  background: #fff;
  -webkit-text-size-adjust: none;
  -webkit-overflow-scrolling: touch;
}
html::-webkit-scrollbar,
body::-webkit-scrollbar {
  width: 0;
  height: 0;
}
body,
h1,
h2,
h3,
h4,
h5,
h6,
hr,
p,
blockquote,
dl,
dt,
dd,
ul,
ol,
li,
pre,
form,
fieldset,
legend,
button,
input,
textarea,
th,
td {
  margin: 0;
  padding: 0;
}
body,
button,
input,
select,
textarea {
  font: 14px/1.5 'microsoft-yahei', '微软雅黑', tahoma, arial, '\5b8b\4f53', helvetica, sans-serif;
}
