}
.browser-tips .wrap {
  width: 100%;
  height: 100%;
}
.browser-tips .tips-content {
  position: absolute;
  left: 50%;
  top: 50%;
  white-space: nowrap;
  -webkit-transform: translate3D(-50%, -50%, 0);
  transform: translate3D(-50%, -50%, 0);
  font-size: 16px;
  text-align: center;
  color: #fff;
}
.browser-tips .tips-content .ico-phone-tips {
  display: inline-block;
  width: 40px;
  height: 40px;
  background: url(data:image/png;base64,) no-repeat center;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  -webkit-animation: swing 1s ease infinite;
          animation: swing 1s ease infinite;
}
.browser-tips .tips-content p {
  width: 100%;
}
.disable-hover {
  pointer-events: none;
}
.disable-scroll {
  height: 100% !important;
  overflow: hidden !important;
}
#css-circle-loader {
  position: relative;
  width: 100px;
  height: 100px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 40px;
  text-align: center;
  font-size: 16px;
  line-height: 100px;
  color: #666;
}
#css-circle-loader div {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  position: absolute;
  top: 0;
  left: 0;
  width: 100px;
  height: 100px;
  border-radius: 50%;
  -webkit-transition: all 0.2s ease-in-out;
  -moz-transition: all 0.2s ease-in-out;
}
#css-circle-loader .bg {
  border: 6px solid #f5f5f5;
}
#css-circle-loader .bg2 {
  border: 6px solid #f5f5f5;
  -webkit-clip: rect(0, 50px, 100px, 0);
  clip: rect(0, 50px, 100px, 0);
}
#css-circle-loader .rount {
  border: #96d251 6px solid;
  -webkit-clip: rect(0, 10px, 100px, 0);
  clip: rect(0, 50px, 100px, 0);
  -webkit-transform: rotate(0deg);
  transform: rotate(0deg);
}
#css-circle-loader .rount2 {
  display: none;
  border: #96d251 6px solid;
  -webkit-clip: rect(0, 100px, 100px, 50px);
  clip: rect(0, 100px, 100px, 50px);
  -webkit-transform: rotate(0deg);
  transform: rotate(0deg);
}
#css-circle-loader .num {
  line-height: 100px;
}
#svg-circle-loader {
  position: relative;
  width: 100px;
  height: 100px;
  margin-left: auto;
  margin-right: auto;
}
#svg-circle-loader #circle-lot {
  -webkit-transform-origin: center;
  transform-origin: center;
  -webkit-transform: rotate(-90deg);
  transform: rotate(-90deg);
}
#svg-circle-loader .percent {
  position: absolute;
  left: 50%;
  top: 50%;
  white-space: nowrap;
  -webkit-transform: translate3D(-50%, -50%, 0);
  transform: translate3D(-50%, -50%, 0);
}
#progress-bar {
  position: fixed;
  left: 0;
  top: 0;
  width: 0;
  height: 4px;
  background: #ea546f;
}
.share-info {
  position: absolute;
  left: -1000px;
  top: -1000px;
  visibility: hidden;
}
.mask {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 20;
  background: rgba(0, 0, 0, 0.7);
}
.alert-pop {
  display: none;
  position: fixed;
  left: 50%;
  top: 50%;
  -webkit-transform: translate3D(-50%, -50%, 0);
  transform: translate3D(-50%, -50%, 0);
  z-index: 30;
  width: 78%;
  max-width: 460px;
  padding: 1rem;
  background: #fff;
  background-size: cover;
  background-repeat: no-repeat;
  border-radius: 3px;
}
.alert-pop .alert-mcbd,
.alert-pop .alert-qctt {
  display: none;
}
.alert-pop .alert-con {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
}
.alert-pop .alert-con img {
  display: block;
  width: 6.4375rem;
  height: 6.4375rem;
  position: absolute;
  left: 50%;
  white-space: nowrap;
  -webkit-transform: translate3D(-50%, 0, 0);
  transform: translate3D(-50%, 0, 0);
  top: -4.3125rem;
}
.alert-pop .alert-con .btn-close {
  position: absolute;
  width: 2rem;
  height: 2rem;
  right: -1.4375rem;
  top: -1.4375rem;
  background-size: cover;
  background-repeat: no-repeat;
}
.alert-pop .alert-con h3 {
  padding: 2.1875rem 0 1.25rem;
  font-size: 0.875rem;
  font-weight: normal;
  color: #333;
  text-align: center;
}
.alert-pop .alert-con .btn-group {
  width: 100%;
  overflow: hidden;
}
.alert-pop .alert-con .btn-group .btn {
  width: 44%;
  padding: 0.5rem 0.9375rem;
  font-size: 0.75rem;
  border-radius: 50px;
}
.alert-pop .alert-con .btn-group .btn:first-of-type {
  float: left;
  color: #333;
  border: 1px solid #eee;
}
.alert-pop .alert-con .btn-group .btn:last-of-type {
  float: right;
  color: #fff;
  background: -webkit-gradient(linear, left top, left bottom, from(#ff6454), to(#ff3f3e));
  background: linear-gradient(top, #ff6454, #ff3f3e);
}
#back2Top {
  opacity: 0;
  position: fixed;
  z-index: 18;
  width: 2.5rem;
  height: 2.5rem;
  bottom: 3.125rem;
  right: 0.9375rem;
  background: rgba(0, 0, 0, 0.6) url(data:image/png;base64,) no-repeat center;
  background-size: 44%;
  border-radius: 2px;
  -webkit-transition: all 0.5s linear;
  transition: all 0.5s linear;
  -webkit-transform: translate3d(6.25rem, 0, 0) rotateZ(360deg);
  transform: translate3d(6.25rem, 0, 0) rotateZ(360deg);
}
#back2Top.roll-in {
  opacity: 1;
  -webkit-transform: translate3d(0, 0, 0) rotateZ(0);
  transform: translate3d(0, 0, 0) rotateZ(0);
}
